import './WelcomePage.css';
import Untitled from '../images/Untitled.svg'

function WelcomePage() {
    return (
        <div className="WelcomePage">
            <div className='Info'>
                <div className='Info-left'>
                    <div className='Info-text'>
                        <span>   </span>
                    </div>
                    <div className='registration'>
                        <button className='come'></button>
                        <button className='Become-a-member'> </button>
                    </div>
                </div>
                       <div className='Info-right'>
                           <a className="Untitled" href="">
                           <img src={Untitled} alt="Untitled" />
                       </a>
                       </div>
            </div>
            <div className='Info-button'>
                <div className='Company-name'> Arman-PV baspasy, 2023</div>
                <div className='subject'>  <button></button></div>
            </div>
        </div>
    );
}

export default WelcomePage;